<template>
  <div>
    <div class="containerBox">
      <!--  -->
      <div class="pageTopic">
        <span class="topicName"></span>
      </div>

      <div class="formBox">
        <el-form ref="caseForm" :model="caseParams" :rules="rules" label-width="80px">
          <el-row>
            <el-col :span="12">
              <el-form-item label="" prop="name">
                <el-input v-model="caseParams.name" placeholder=""></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="phoneNo">
                <el-input v-model.number="caseParams.phoneNo" placeholder=""></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="" prop="age">
                <el-input v-model.number="caseParams.age" placeholder=""></el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="sex">
                <el-radio v-model="caseParams.sex" :label="1"></el-radio>
                <el-radio v-model="caseParams.sex" :label="2"></el-radio>
              </el-form-item>
            </el-col>
          </el-row>
          <el-row>
            <el-col :span="12">
              <el-form-item label="" prop="height">
                <el-input v-model="caseParams.height" placeholder="">
                  <template slot="append">/cm</template>
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="12">
              <el-form-item label="" prop="weight">
                <el-input v-model="caseParams.weight" placeholder="">
                  <template slot="append">/kg</template>
                </el-input>
              </el-form-item>
            </el-col>
          </el-row>

          <el-form-item>
            <el-button type="primary" @click="onSubmit"></el-button>
            <el-button @click="back"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </div>
  </div>
</template>
<script>
import Vditor from "vditor"
import "vditor/dist/index.css"
import AppServerInf from "@/common/serverinf"
import AppConfig from '@/common/config'

export default {
  data() {
    return {
      caseParams: {
        name: "",
        phoneNo: '',
        age: "",
        sex: 1,
        height: "",
        weight: "",
        //startDate: "",
        //treatmentNum: 0,
      },

      rules:{
        name: [{ required: true, message: '', trigger: 'blur'}],
        phoneNo: [{ required: true, type:'number', message: ''}],
        age: [{ required: true, type:'number', message: ''}],
        height: [{ required: true, type:'number', transform(value) {return Number(value)}, message: ''}],
        weight: [{ required: true, type:'number', transform(value) {return Number(value)}, message: ''}],
      },

    };
  },
  created() {
    const caseId = this.$route.params.caseId
    if(caseId) {
      this.initCaseParams(caseId)
    }
  },
  methods: {
    initCaseParams(caseId) {
      this.caseParams = this.$db.case(caseId)
    },

    onSubmit() {
      this.$refs['caseForm'].validate(valid=>{
        if (!valid) {
          return false
        }
        //
        this.$db.saveCase(this.caseParams)
        this.$modal.alert("")
        this.back()
      })
    }
  },
}
</script>
<style>

</style>



